.content {
  padding: 24px;
}
.content .indexList > li,
.content .articleList > li {
  list-style-type: decimal;
}
.content .indexList .anchorBar,
.content .articleList .anchorBar {
  color: var(--blue);
}
.content .articleList > li::marker {
  font-size: 1.5em;
  font-weight: bold;
}
.content .anchorTitle {
  transform: translateY(-0.8em);
}
